import React, { useState } from "react";
import axios from "axios";
import { motion } from "framer-motion";

export default function VideoInfo({ mediaInfo, videoId }) {
  const [showDownloadLink, setShowDownloadLink] = useState(false);
  const [downloadLink, setDownloadLink] = useState("");
  const [loading, setLoading] = useState(false);
  axios.defaults.baseURL =
    "https://youtube-to-mp4.p.rapidapi.com/url=&title?url=https://www.youtube.com";

  function handleSubmit(e) {
    e.preventDefault();
    setLoading(true);

    axios
      .get(
        `/watch?v=${videoId}`,

        {
          headers: {
            "X-RapidAPI-Key":
              "4b68289c3dmshe170ce743f6e485p1f0d36jsn852c82c78b6b",
          },
        }
      )
      .then((res) => {
        console.log(res.data);
        setShowDownloadLink(true);
        setDownloadLink(res.data);

        if (window.location.href.indexOf("?") > -1) {
          window.history.pushState(
            {},
            document.title,
            window.location.pathname
          );
        }
      })
      .catch((err) => console.log("error"));
  }

  return (
    <div>
      <p>channel Title : {mediaInfo.channelTitle}</p>
      <p>title : {mediaInfo.title}</p>
      <p>published At: {mediaInfo.publishedAt}</p>
      {showDownloadLink ? (
        <p>
          download link :<a href={downloadLink}>{downloadLink}</a>
        </p>
      ) : (
        <button onClick={handleSubmit} className="btn" disabled={loading}>
          {loading && (
            <motion.i
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 0.5 }}
              className="fa fa-refresh fa-spin"
            ></motion.i>
          )}
          Download
        </button>
      )}
    </div>
  );
}
