import React, { useEffect } from "react";
import axios from "axios";

export default function Search({
  videoId,
  setVideoId,
  setMediaInfo,
  setThumbnail,
  setShowInfo,
  setInputText,
  inputText,
  setShowAlert,
  setErrorMessage,
}) {
  useEffect(() => {
    axios
      .get(
        `https://www.googleapis.com/youtube/v3/videos?part=snippet&id=${videoId}&key=AIzaSyBgk17YpqGq9ftG32oY9n3OLpqgmyXb8wk`
      )
      .then((res) => {
        setMediaInfo(res.data.items[0].snippet);
        setThumbnail(res.data.items[0].snippet.thumbnails.standard.url);
        setShowInfo(true);
      })
      .catch((err) => console.log("error"));
  }, [videoId]);

  function handleInputText(e) {
    setInputText(e.target.value);
  }

  function handleSubmit(e) {
    e.preventDefault();
    let sampleYoutubeUrl = "https://www.youtube.com/watch?";
    let sampleYoutubeUrl2 = "https://youtu.b";

    if (inputText.length === 0) {
      console.log("text khaliye");
      setErrorMessage("the input text is empty");
      setShowAlert(true);
      return;
    }

    if (sampleYoutubeUrl2 === inputText.split("e/")[0]) {
      setVideoId(inputText.split("e/")[1].substring(0, 11));
      setInputText("");
      return;
    }

    if (sampleYoutubeUrl === inputText.split("v=")[0]) {
      setVideoId(inputText.split("v=")[1].substring(0, 11));
      setInputText("");
      return;
    } else {
      setErrorMessage("The link you entered is incorrect");
      setShowAlert(true);
      setInputText("");
    }
  }

  return (
    <div>
      <form>
        <input
          type="text"
          onChange={handleInputText}
          placeholder="Enter the link"
        />
        <button className="btn" onClick={handleSubmit} type="submit">
          Search Url
        </button>
      </form>
    </div>
  );
}
